.imageupload{
    display: flex;
    flex-direction: column;
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 10px;
    margin-top: 10px;
    background: white;
}

#cont{
    width: 580px;
    border: 1px solid lightgray;
    padding: 10px;
    background: white;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 20px;
    margin-top: 30px;
}

.imageupload_progress{
    width: 100%;
}

input{
    width: 150% !important;
}
.MuiInputBase-root{
    margin: 0px !important;
    margin-top: 10px !important;
    margin-bottom: 10px !important;
}
.img_brand{
    text-align: left;
    display: flex;
    justify-content: flex-end;
}
h1{
    text-align: center;
    font-size: 20px;
    margin-bottom: 5px;
}

@media only screen and (max-width:1000px){
    .imageupload{
        width: 95%;
    }
    input{
        font-size: 1.1rem;
    }
    .MuiInputBase-root{
        font-size: 1.1rem !important;
    }
    #cont{
        width: 95vw;
        padding: 0;
    }
}