.post{
    background-color: white;
    border: 1px solid lightgray;
    margin-bottom: 15px;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    width: 600px;
    max-width: 650px;
    object-fit: contain;
    background: #fafafa;
}

.post_img{
    width: 100%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
}
.post_text{
    font-weight: normal;
    padding: 15px;
    font-size: 20px;
}
.post_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
}
.post_header_1{
    display: flex;
    align-items: center;
}
.post_avatar{
    margin-right: 10px;
}

.MuiInputBase-root {
    margin: 10px;
}

.post_commentBox{
    display: flex;
    margin-top: 10px;
}

.post_input{
    flex: 0.85;
    border: none;
    padding: 10px;
    outline: none;
    border-top: 1px solid lightgray;
}

.post_button{
 flex: 0.15;
 border: none;
 color: #6082a3;
 border-top: 1px solid lightgray;
 background-color: transparent;
 cursor: pointer;
}
.post_comments{
    padding: 14px;
}
.MuiButton-containedSecondary{
    background-color: #d33c3c !important;
}

@media only screen and (max-width:1000px){
    .post{
        width: 95vw;
        font-size: 1rem;
    }
    .post_header{
        font-size: 1.2rem;
    }
    .post_text{
        font-size: 1.2rem;
    }
    .MuiAvatar-root{
        width: 50px;
        object-fit: contain;
    }
    .post_comments{
        font-size: 1.15rem;
    }
    .post_input{
        font-size: 1.1rem;
    }
    .post_button{
        font-size: 1.1rem;
    }
}