*{
  margin: 0;
  padding: 0;
  max-width: 100vw;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

html{
  scroll-behavior: smooth;
}

.App{
  background-color: #fafafa;
  height: 100vh;
}
#app_header{
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: white;
  padding: 14px 17px;
  /* border-bottom: 1px solid lightgray; */
  margin-bottom: -12px;
  object-fit: contain;
  display: flex;
  justify-content: space-between;
  align-items: center;
      box-shadow: 0 1px 2px 0 rgba(60,64,67,.3), 0 2px 6px 2px rgba(60,64,67,.15);
}

.MuiLinearProgress-root {
  opacity:0 !important;
  z-index:1000;
  height: 2.7px !important;
  position: sticky !important;
  top: 64.2px !important;
}

.MuiLinearProgress-colorPrimary {
  background-color: rgb(250 250 250) !important;
}
.MuiLinearProgress-barColorPrimary {
  background-color: #039be5 !important;
}
.MuiLinearProgress-barColorPrimary {
  background-color: #039be5 !important;
}

.hidder{
 animation: loaders 3.2s  ;
 animation-fill-mode: forwards;
}

@keyframes loaders{
0%{
  opacity: 1;
}  
99%{
  opacity: 1;
}
100%{
  opacity: 0;
  /* background-color: white; */
}

}

.app_signUp{
  display: flex;
  flex-direction: column;
}

.header_image{
  object-fit: contain;
}

.app_posts{
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #fafafa;
}

button{
  text-transform: capitalize !important;
  padding-right: 10px !important;
  padding-left: 10px !important;
}

.app_right{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.app_right> .MuiAvatar-root{
  margin-right: auto;
  margin-left: 20px;
}
h3 > a{
  text-decoration: none;
  color: black;
}


@media only screen and (max-width:1000px){
  #app_header{
    padding: 17px;
  }
  .MuiButton-label{
    font-size: 1.2rem;
  }
  .makeStyles-paper-1{
    width: 80vw !important;
  }
  .jss1{
    width: 80vw !important;
  }
  .app_signUp input{
    font-size: 1.3rem;
  }
  .MuiInputBase-root, .MuiInput-root, .MuiInput-underlinet{
    margin-top: 20px !important;
    margin-bottom: 20px !important;
  }
  .MuiLinearProgress-root {
    top: 79px !important;
  }
}